
@mixin clearfix() {
  &:before,
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

@mixin appearance($value) {
  webkit-appearance: $value;
  -moz-appearance:    $value;
  appearance:         $value;
}
@mixin gadientBox() {
  content:"";
  position: absolute;
  left:0;
  bottom:0;
  display: block;
  z-index: 1;
  width: 100%;
  height: 50%;
  pointer-events: none;
  opacity: 0.8;
  background-image: linear-gradient(-180deg, rgba(0,0,0,0.00) 0%, #000000 100%);
}

@mixin scale($value) {
  -webkit-transform: scale($value);
  -moz-transform: scale($value);
  -ms-transform: scale($value);
  -o-transform: scale($value);
  transform: scale($value);
}

@mixin scale2D($value, $value2) {
  -webkit-transform: scale($value, $value2);
  -moz-transform: scale($value, $value2);
  -ms-transform: scale($value, $value2);
  -o-transform: scale($value, $value2);
  transform: scale($value, $value2);
}


@mixin translateX($left) {
  -webkit-transform: translateX($left);
  -moz-transform: translateX($left);
  -ms-transform: translateX($left);
  -o-transform: translateX($left);
  transform: translateX($left);
}

@mixin translateY($top) {
  -webkit-transform: translateY($top);
  -moz-transform: translateY($top);
  -ms-transform: translateY($top);
  -o-transform: translateY($top);
  transform: translateY($top);
}
@mixin transform($top,$left) {
  -webkit-transform: translate($top,$left);
  -moz-transform: translate($top,$left);
  -ms-transform: translate($top,$left);
  -o-transform: translate($top,$left);
  transform: translate($top,$left);
}

@mixin translate2D($x, $y) {
  -webkit-transform: translate($x, $y);
  -moz-transform: translate($x, $y);
  -ms-transform: translate($x, $y);
  -o-transform: translate($x, $y);
  transform: translate($x, $y);
}

@mixin translate3D($x, $y, $z) {
  -webkit-transform: translate3d($x, $y, $z);
  -moz-transform: translate3d($x, $y, $z);
  -ms-transform: translate3d($x, $y, $z);
  -o-transform: translate3d($x, $y, $z);
  transform: translate3d($x, $y, $z);
}

@mixin rotate($deg) {
  -webkit-transform: rotate($deg);
  -moz-transform: rotate($deg);
  -ms-transform: rotate($deg);
  -o-transform: rotate($deg);
  transform: rotate($deg);
}

@mixin perspective($value) {
  -moz-perspective: $value;
  -webkit-perspective: $value;
  perspective: $value;
}
@mixin rotateY($deg) {
  -webkit-transform: rotateY($deg);
  -moz-transform: rotateY($deg);
  -ms-transform: rotateY($deg);
  -o-transform: rotateY($deg);
  transform: rotateY($deg);
}

@mixin transition($time) {
  -webkit-transition: all $time ease-out;
  -moz-transition: all $time ease-out;
  -o-transition: all $time ease-out;
  transition: all $time ease-out;
}

@mixin transitionAttr($attr, $time) {
  -webkit-transition: $attr $time ease-out;
  -moz-transition: $attr $time ease-out;
  -o-transition: $attr $time ease-out;
  transition: $attr $time ease-out;
}

@mixin border-radius($radius) {
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
  border-radius: $radius;
}

@mixin border-radius-top($radius) {
  -webkit-border-top-left-radius: $radius;
  -webkit-border-top-right-radius: $radius;
  -moz-border-radius-topleft: $radius;
  -moz-border-radius-topright: $radius;
  border-top-left-radius: $radius;
  border-top-right-radius: $radius;
}

@mixin border-radius-bottom($radius) {
  -webkit-border-bottom-right-radius: $radius;
  -webkit-border-bottom-left-radius: $radius;
  -moz-border-radius-bottomright: $radius;
  -moz-border-radius-bottomleft: $radius;
  border-bottom-right-radius: $radius;
  border-bottom-left-radius: $radius;
}

@mixin opacity($op) {
  opacity: $op;
  filter: alpha(opacity=$op);
}

@mixin transition($time) {
  -webkit-transition: all $time ease;
  transition: all $time ease;
}

@mixin ul-clean () {
  list-style: none;
  margin: 0;
  padding: 0;
}

@mixin three-dots () {
  white-space: nowrap;
  overflow:hidden !important;
  text-overflow: ellipsis;
}


// --------------------------------------------------
// Flexbox SASS mixins
// The spec: http://www.w3.org/TR/css3-flexbox
// --------------------------------------------------

// Flexbox display
@mixin flexbox() {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}

// Flexbox display
@mixin filter-blur($value) {
  -webkit-filter: blur($value);
  -moz-filter: blur($value);
  -o-filter: blur($value);
  -ms-filter: blur($value);
  filter: blur($value);
}

// The 'flex' shorthand
// - applies to: flex items
// <positive-number>, initial, auto, or none
@mixin flex($values) {
  -webkit-box-flex: $values;
  -moz-box-flex: $values;
  -webkit-flex: $values;
  -ms-flex: $values;
  flex: $values;
}

// Flex Flow Direction
// - applies to: flex containers
// row | row-reverse | column | column-reverse
@mixin flex-direction($direction) {
  -webkit-flex-direction: $direction;
  -moz-flex-direction: $direction;
  -ms-flex-direction: $direction;
  flex-direction: $direction;
}

// Flex Line Wrapping
// - applies to: flex containers
// nowrap | wrap | wrap-reverse
@mixin flex-wrap($wrap) {
  -webkit-flex-wrap: $wrap;
  -moz-flex-wrap: $wrap;
  -ms-flex-wrap: $wrap;
  flex-wrap: $wrap;
}

// Flex Direction and Wrap
// - applies to: flex containers
// <flex-direction> || <flex-wrap>
@mixin flex-flow($flow) {
  -webkit-flex-flow: $flow;
  -moz-flex-flow: $flow;
  -ms-flex-flow: $flow;
  flex-flow: $flow;
}

// Display Order
// - applies to: flex items
// <integer>
@mixin order($val) {
  -webkit-box-ordinal-group: $val;
  -moz-box-ordinal-group: $val;
  -ms-flex-order: $val;
  -webkit-order: $val;
  order: $val;
}

// Flex grow factor
// - applies to: flex items
// <number>
@mixin flex-grow($grow) {
  -webkit-flex-grow: $grow;
  -moz-flex-grow: $grow;
  -ms-flex-grow: $grow;
  flex-grow: $grow;
}

// Flex shrink
// - applies to: flex item shrink factor
// <number>
@mixin flex-shrink($shrink) {
  -webkit-flex-shrink: $shrink;
  -moz-flex-shrink: $shrink;
  -ms-flex-shrink: $shrink;
  flex-shrink: $shrink;
}

// Flex basis
// - the initial main size of the flex item
// - applies to: flex itemsnitial main size of the flex item
// <width>
@mixin flex-basis($width) {
  -webkit-flex-basis: $width;
  -moz-flex-basis: $width;
  -ms-flex-basis: $width;
  flex-basis: $width;
}

// Axis Alignment
// - applies to: flex containers
// flex-start | flex-end | center | space-between | space-around
@mixin justify-content($justify) {
  -webkit-justify-content: $justify;
  -moz-justify-content: $justify;
  -ms-justify-content: $justify;
  justify-content: $justify;
  -ms-flex-pack: $justify;
}

// Packing Flex Lines
// - applies to: multi-line flex containers
// flex-start | flex-end | center | space-between | space-around | stretch
@mixin align-content($align) {
  -webkit-align-content: $align;
  -moz-align-content: $align;
  -ms-align-content: $align;
  align-content: $align;
}

// Cross-axis Alignment
// - applies to: flex containers
// flex-start | flex-end | center | baseline | stretch
@mixin align-items($align) {
  -webkit-align-items: $align;
  -moz-align-items: $align;
  -ms-align-items: $align;
  -ms-flex-align: $align;
  align-items: $align;
}

// Cross-axis Alignment
// - applies to: flex items
// auto | flex-start | flex-end | center | baseline | stretch
@mixin align-self($align) {
  -webkit-align-self: $align;
  -moz-align-self: $align;
  -ms-align-self: $align;
  align-self: $align;
}
// Placeholder text
@mixin placeholder($color: $placeholder, $opacity: 1) {
  // Firefox
  &::-moz-placeholder {
    color: $color;
    opacity: $opacity; // Override Firefox's unusual default opacity; see https://github.com/twbs/bootstrap/pull/11526
  }
  &:-ms-input-placeholder { color: $color; opacity: $opacity;  } // Internet Explorer 10+
  &::-webkit-input-placeholder  { color: $color;  opacity: $opacity; } // Safari and Chrome
}
@mixin switch($width,$height,$bgcolor,$color) {
  position: relative;
  @include flexbox();
  @include align-items(center);
  &.reverse {
    @include flex-direction(row-reverse);
    label {
      margin: 0 0 0 8px;
    }
  }
  label {
    margin: 0 8px 0 0 ;
    font-size: 12px;
    span {
      &:first-child {

      }
      &:last-child {
        display: none;
      }
    }
  }
  input {
    height: $height;
    width: 100%;
    position: absolute;
    top: 0;
    z-index: 2;
    opacity: 0;
    cursor: pointer;
    &:checked {
      & + label {
        span:first-child {
          display: none;
        }
        span:last-child {
          display: block;
        }
      }
      & ~ .toggle-outside {
        background:$bgcolor;
      }
      & ~  .toggle-outside .toggle-inside {
        left: $width/2 + 2;
      }
    }
  }
  .toggle-outside {
    background: #ebebeb;
    position: relative;
    width: $width;
    border-radius: 30px;
    height: $height;
    overflow: hidden;
    transition: 0.25s ease all;
  }
  .toggle-inside {
    box-shadow: -1px 1px 3px 0 rgba(0,0,0,0.20);
    border-radius: 50%;
    background: $color;
    position: absolute;
    transition: 0.25s ease all;
    height:$height - 4;
    width: $height - 4;
    left: 2px;
    top: 50%;
    @include translateY(-50%);
  }
}
@mixin  user-select($value) {
  -webkit-user-select: $value;
  -moz-user-select: $value;
  -ms-user-select: $value;
  user-select: $value;
}

@mixin checkbox($size:24px,$border-size: 1px,$tick-size:12px,$color: #919899,$top:0px) {
  display: block;
  position: relative;
  padding-left: $size + 22px;
  cursor: pointer;
  margin: 0;
  min-height: $size;
  line-height: $size;
  text-align: left;

  max-width: 400px;
  @include user-select(none);
  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
    &:disabled {
      opacity: 0.7;
      &+ .ck {
        opacity: 0.7;
      }
      &+ .ck + span:not(.error) {
        opacity: 0.7;
      }
    }
    &:checked {
      &+ .ck {
        background: transparent;
        border-color: $color;
        &:before {
          opacity: 1;
        }
      }
      &+ .ck + span:not(.error) {
        color: $headings-color;
      }
    }
    &.error {
      & + .ck {
        background: #ffecee;
        border-color: #ffecee;
      }
      &+ .ck + span:not(.error) {
        color:$red;
      }
    }
    &.success {
      & + .ck {
        background: #eefdf5;
        border-color:#eefdf5;
        &:before {
          border-color:$green;
        }
      }
      &+ .ck + span:not(.error) {
        color:$green;
      }
    }
  }
  .ck {
    position: absolute;
    left:0;
    top:$top;
    display: block;
    width:$size;
    height: $size;
    background: transparent;
    border:$border-size solid #919899;
    &:before {
      display: block;
      content: "";
      width: $tick-size;
      height: $tick-size/2;
      border-left: $border-size solid white;
      border-bottom: $border-size solid white;
      position: absolute;
      left: 50%;
      top: 44%;
      transform: translate(-50%, -50%) rotate(-45deg);
      transition: all 0.3s linear;
      opacity: 0;
    }
  }

}

@mixin radio($size:20px,$border-size: 2px,$tick-size:32px,$color: $blue,$top:0px) {
  input {

    &:checked {
      & + span.ck {
        border-radius: 50%;
        background: $color;
        border:0 solid $color;
      }
    }
    &.success {
      & + span.ck {
        background: #eefdf5;
        border-color:#eefdf5;
        &:before {
          background: $green;
        }
      }
    }
  }
  span.ck {

    &:before {
      height: 10px;
      width: 10px;
      top: 50%;
      @include transform(-50%, -50%);
      border-radius:  50%;
      background: white;
      border: 0 solid white;
    }
  }
}

@mixin dotdotdot($font-size: 16px, $line-height: 1.4, $lines-to-show: 1, $excerpt-bg: transparent) {
  background: $excerpt-bg;
  /// Fallback for non-webkit /
  display: block;
  display: -webkit-box;
  max-height: $font-size*$line-height*$lines-to-show ;
  /// Fallback for non-webkit /
  font-size: $font-size ;
  line-height: $line-height;
  -webkit-line-clamp: $lines-to-show;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  position: relative;
  overflow: hidden;
  &:before {
    background: $excerpt-bg;
    bottom: 0;
    position: absolute;
    right: 0;
    float: right;
    content: '\2026';
    margin-left: -10rem;
    width: 3rem;
  }
  &:after {
    content:'';
    background: $excerpt-bg;
    position: absolute;
    height: 50px;
    width: 100%;
    z-index: 1;
  }
